// eslint-disable-next-line

import { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';


let 박스 = styled.div`
 padding : 20px;
`;
let 제목 = styled.h5`
 font-size : 25px;
`;





function Detail(props){
  let [alert, alert변경] = useState(true);
  let [inputData, inputData변경] = useState();

  useEffect(()=>{
    //alert창이 2초 후에 사라지게 하기
    let timer = setTimeout(()=>{ alert변경(false)}, 2000);
    return ()=>{ clearTimeout(timer)}
  },[alert]);//alert라는 state가 변경 될 때 마다 실행해주세요(update문제해결)

  let { id } = useParams(); 

  //1. find는 Array안에서 원하는 자료를 찾고 싶을 때 (ES6)
  //2. find()는 array뒤에 붙일 수 있고 콜백함수가 들어감.
  //3. 콜백함수 내의 파라미터(상품)는 array안에 있던 하나하나의 데이터를 의미.
  //4. return 오른쪽에는 조건식을 적을 수 있음. true만 변수에 저장.
  let found = props.shoes.find(function(products){
    return products.id == id
  });
  let history = useHistory(); //방문기록 등을 저장해놓은 오브젝트

  return(
    <div className="container">
      <박스>
        <제목 className="red">상세페이지</제목>
      </박스>
      {inputData}
      <input onChange={(e)=>{ inputData변경(e.target.value) }} />

      {
        alert === true
        ? (<div className='my-alert-gold'>
            <p>재고가 얼마 남지 않았습니다</p>
           </div>)
        : null
      }



      <div className="row">
        <div className="col-md-6">
          <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{found.title}</h4>
          <p>{found.content}</p>
          <p>{found.price}원</p>

           <Info 재고={props.재고}></Info>         

          <button className="btn btn-danger" onClick={()=>{ props.재고변경([9,11,12]) }}>주문하기</button> 
          <button className="btn btn-danger" onClick={()=>{
            history.goBack();
          }}>뒤로가기</button> 
        </div>
      </div>
    </div>
  )

}

function Info(props){
  return(
    <p>재고: {props.재고[0]}</p>
  )
}


export default Detail;